
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  background: #eee;
  margin: 0;
  padding: 0;
}
* {
  box-sizing: border-box;
}
.bg {
  position: fixed;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-repeat: no-repeat;
  background-color: #666;
  background-size: cover;
}



